@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700&display=swap');

.newsletter-signup {
    display: flex;
    justify-content: center;
    background-color: #FAFAFA;
    border-top: 4px solid #EFEFEF;
    border-bottom: 2px solid #EFEFEF;
    max-width: 100%;
    margin: 0 auto;
    margin-top: 40px;
    height: 120px;

    .newsletter-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: auto;
        height: auto;
        border-top: 4px;
        border-bottom: 2px;

        h1 {
            font-size: 24px;
            font-weight: 700;
            font-family: 'Titillium Web', Arial, Helvetica, sans-serif;
            margin-bottom: 10px;
            text-align: center;
        }

        .input-button-container {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
        }

        input[type='email'] {
            flex: 1;
            border: 1px solid #353535;
            border-radius: 4px;
            font-size: 14px;
            width: 204px;
            height: 28px;
            margin-right: 15px;

            &::placeholder {
                font-family: 'Titillium Web', Arial, Helvetica, sans-serif;
                font-size: 14px;
            }
        }

        .nl-btn {
            width: 79px;
            height: 30px;
            border: none;
            background-color: #FAA500;
            color: #fff;
            font-size: 14px;
            font-weight: 700;
            font-family: 'Titillium Web', Arial, Helvetica, sans-serif;
            border-radius: 4px;
            margin-bottom: 10px;
            cursor: pointer;
        }

        .success-message {
            margin-top: 0.5rem;
            color: #28a745;
        }
    }
}

// Estilo para dispositivos móveis
@media (max-width: 768px) {
    .newsletter-signup {
        width: 100%;
    }

    .newsletter-container {
        flex-direction: column;

        .input-button-container {
            flex-direction: column;
            align-items: stretch;
        }

        input[type='email'] {
            margin-right: 0;
            margin-bottom: 10px;
        }

        .nl-btn {
            margin-left: 0;
        }
    }
}